//------------------------------------------------------------------------------
// @object: Bar
//------------------------------------------------------------------------------
// @description
//
//------------------------------------------------------------------------------

//
// @settings

// Layout Variables
$bar-spacing: 16px !default;
$bar-size: 40px !default;
$bar-profile-max: 296px !default;


// Theme Variables
$bar-bg-color: $default-action-bg !default;
$bar-bg-hover-color: $default-action-bg-hover !default;
$bar-color: $default-action-color !default;
$bar-hover-color: $default-action-color-hover !default;
$bar-user-title-color: $default-text-color !default;


//
// @scss
.o-bar {
	background-color: $bar-bg-color;
	flex-wrap: nowrap;

	&-left,
	&-right {
		flex: 1;
		margin: 0;
		padding: 0;
		list-style: none;
	}

	&-left,
	&-left &-item {
		float: left;
	}

	&-right,
	&-right &-item {
		float: right;
	}

	&-info {
		display: inline-flex;
		padding: $bar-spacing;
		padding-right: 0;

		&-icon {
			margin-right: ($bar-spacing / 2);
		}
	}

	&-action {
		color: $bar-color;
		display: inline-flex;
		padding: $bar-spacing;

		&:hover,
		&:focus {
			background-color: $bar-bg-hover-color;
			color: $bar-hover-color;
		}
	}

	&-avatar {
		display: inline-block;
		width: $bar-size;
		height: $bar-size;
		margin: 0 ($bar-spacing / 2) 0 0;
		padding: 0;
	}

	&-title {
		@include type(title, $lh: 1);
		float: left;
		margin-bottom: 0;
		padding: $bar-spacing;
		@include child();
	}

	&-profile {
		max-width: $bar-profile-max;
		padding: ($bar-spacing / 2) $bar-spacing;
	}

	&-user {
		@include type(body2, $lh: 1.35);
		vertical-align: middle;
		display: inline-block;
		padding: 0 $bar-spacing 0 0;

		&-title {
			@include type(caption);
			color: $bar-user-title-color;
			opacity: $default-opacity-54;
			display: block;
		}
	}

	@include parent();
}
