.o-avatar {
	vertical-align: middle;
	border: 2px solid #ffffff;
	border-radius: 50%;
}

.o-avatar-wrap {
	position: relative;
	width: 60%;
	margin: 0 auto;
}

.o-avatar-online {
	background-color: #cfdd3c;
	border: 2px solid #ffffff;
	border-radius: 50%;
	position: absolute;
	z-index: 101;
	top: 10%;
	right: 0;
	float: left;
	padding: 8%;
}

.o-avatar-rate-icon {
	margin-right: 0;
}

.o-avatar-rate-good, .o-avatar-rate-bad {
	border: 2px solid #ffffff;
	border-radius: 50%;
	color: #ffffff;
	position: absolute;
	z-index: 101;
	bottom: 0;
	float: left;
	padding: 8px;
}

.o-avatar-rate-good {
	background-color: #5eba66;
}

.o-avatar-rate-good:hover, .o-avatar-rate-good:focus {
	background-color: #97d29c;
	color: #ffffff;
}

.o-avatar-rate-bad {
	background-color: #e23936;
	right: 0;
}

.o-avatar-rate-bad:hover, .o-avatar-rate-bad:focus {
	background-color: #ec7f7d;
	color: #ffffff;
}

.o-bar {
	background-color: #f5f5f5;
	flex-wrap: nowrap;
	display: flex;
	flex-direction: row;
}

.o-bar-left, .o-bar-right {
	flex: 1;
	margin: 0;
	padding: 0;
	list-style: none;
}

.o-bar-left,
.o-bar-left .o-bar-item {
	float: left;
}

.o-bar-right,
.o-bar-right .o-bar-item {
	float: right;
}

.o-bar-info {
	display: inline-flex;
	padding: 16px;
	padding-right: 0;
}

.o-bar-info-icon {
	margin-right: 8px;
}

.o-bar-action {
	color: #757575;
	display: inline-flex;
	padding: 16px;
}

.o-bar-action:hover, .o-bar-action:focus {
	background-color: #eeeeee;
	color: #258cf4;
}

.o-bar-avatar {
	display: inline-block;
	width: 40px;
	height: 40px;
	margin: 0 8px 0 0;
	padding: 0;
}

.o-bar-title {
	font-size: 20px;
	letter-spacing: 0.02em;
	font-weight: 500;
	line-height: 1;
	float: left;
	margin-bottom: 0;
	padding: 16px;
	flex: 1;
	align-self: auto;
}

.o-bar-profile {
	max-width: 296px;
	padding: 8px 16px;
}

.o-bar-user {
	font-size: 14px;
	letter-spacing: 0.04em;
	font-weight: 500;
	line-height: 1.35;
	vertical-align: middle;
	display: inline-block;
	padding: 0 16px 0 0;
}

.o-bar-user-title {
	font-size: 12px;
	letter-spacing: 0.08em;
	font-weight: 400;
	line-height: 1.6666666667;
	color: #212121;
	opacity: 0.54;
	display: block;
}

.c-forum {
	background-color: #ffffff;
	border-radius: 2px;
	position: relative;
	margin-bottom: 16px;
	display: flex;
	flex-direction: column;
	box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
}

.c-forum-body {
	flex: 1 1 auto;
	margin-bottom: 16px;
}

.c-forum-media {
	float: left;
	margin: 16px 16px 0;
	display: flex;
	flex-direction: column;
	flex: 1;
	align-self: auto;
}

.c-forum-media-icon {
	color: #e8306e;
	width: 40px;
	height: 40px;
}

.c-forum-media-icon-read {
	color: #258cf4;
}

.c-forum-info {
	margin-right: 16px;
	display: flex;
	flex-direction: column;
}

.c-forum-title {
	font-size: 24px;
	letter-spacing: normal;
	font-weight: 400;
	line-height: 1.3333333333;
	margin-top: 24px;
	margin-bottom: 12px;
}

.c-forum-title-link {
	line-height: 1;
}

@media (min-width: 992px) {
	.c-forum-title {
		margin-bottom: 0;
	}
}

.c-forum-text {
	font-size: 14px;
	letter-spacing: 0.04em;
	font-weight: 400;
	line-height: 1.4285714286;
	margin: 16px 0 0;
}

.c-forum-footer {
	border-top: 1px solid rgba(0, 0, 0, 0.125);
	border-radius: 0 0 2px 2px;
}

.c-forum-cat-title {
	font-size: 24px;
	letter-spacing: normal;
	font-weight: 400;
	line-height: 1.3333333333;
	border-bottom: 1px solid rgba(0, 0, 0, 0.125);
	margin-top: 16px;
	margin-bottom: 24px;
	padding-bottom: 4px;
}

.c-forum-cat-title-link {
	line-height: 1;
}

@media (min-width: 992px) {
	.c-forum-deck {
		flex-wrap: wrap;
		display: flex;
		flex-direction: row;
	}
	.c-forum-deck .c-forum {
		flex: 1 0 0%;
		min-width: calc((100% / 2) - (12px));
		max-width: calc((100% / 2) - (12px));
		margin-bottom: 24px;
		margin-left: 24px;
		display: flex;
		flex-direction: column;
	}
	.c-forum-deck .c-forum:first-child, .c-forum-deck .c-forum:nth-child(2n+1) {
		margin-left: 0;
	}
}

.c-topic {
	border-radius: 2px;
	margin: 0;
	margin-bottom: 24px;
	padding: 0;
	list-style: none;
	box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
}

.c-topic-item {
	font-size: 16px;
	letter-spacing: 0.04em;
	font-weight: 400;
	line-height: 1.75;
	border-top: 1px solid rgba(0, 0, 0, 0.125);
	min-height: 72px;
	padding: 16px;
	display: flex;
	flex-direction: row;
	flex: 1;
	align-self: auto;
}

.c-topic-item:hover {
	background-color: #fafafa;
}

.c-topic-type {
	padding-right: 16px;
}

.c-topic-type-icon {
	color: #e8306e;
	width: 40px;
	height: 40px;
}

.c-topic-type-icon-read {
	color: #258cf4;
}

.c-topic-primary {
	margin: 0;
	flex: 1;
	align-self: auto;
}

.c-topic-title {
	font-size: 16px;
	letter-spacing: 0.04em;
	font-weight: 400;
	line-height: 1.5;
	margin: 0;
}

.c-topic-title-link:hover {
	text-decoration: underline;
}

.c-topic-title-sub {
	font-size: 14px;
	letter-spacing: 0.04em;
	font-weight: 400;
	line-height: 1.5;
	margin: 0;
	flex: 1;
	align-self: auto;
}

.c-topic-title-data {
	display: inline-block;
	padding-right: 16px;
}

.c-topic-title-data-time {
	display: none;
}

@media (min-width: 992px) {
	.c-topic-title-data-time {
		display: inline-block;
	}
}

.c-topic-title-data-views {
	display: none;
}

@media (min-width: 768px) {
	.c-topic-title-data-views {
		display: inline-block;
	}
}

.c-topic-title-data-icon {
	width: 18px;
	height: 18px;
	margin-right: 8px;
}

.c-topic-title-data-link:hover {
	text-decoration: underline;
}

.c-topic-secondary {
	display: inline-flex;
	flex-wrap: nowrap;
	margin-left: 8px;
}

.c-topic-secondary-user {
	font-size: 14px;
	letter-spacing: 0.04em;
	font-weight: 500;
	line-height: 0.9;
	text-align: right;
	vertical-align: middle;
	display: inline-block;
	align-self: center;
	padding: 0 0 0 16px;
}

.c-topic-secondary-date {
	font-size: 12px;
	letter-spacing: 0.08em;
	font-weight: 400;
	line-height: 1.6666666667;
	text-align: right;
	display: block;
}

.c-topic-secondary-avatar {
	display: inline-block;
	align-self: center;
	width: 44px;
	height: 44px;
	margin: 0 8px 0 0;
	padding: 0;
}

.c-topic-secondary-link:hover {
	text-decoration: underline;
}

.paging {
	display: inline-flex;
	margin: 0;
	padding: 0;
	list-style: none;
	align-content: center;
}

.paging-icon {
	margin-right: 8px;
}

.paging-item {
	margin: 0 2px;
}

.paging-link {
	font-size: 12px;
	letter-spacing: 0.08em;
	font-weight: 400;
	line-height: 1.6666666667;
	background: #eeeeee;
	border-radius: 2px;
	margin: 0;
	padding: 2px 4px;
}

.paging-link:hover {
	text-decoration: none;
	background: #bdbdbd;
}

.paging-link-icon {
	width: 18px;
	height: 18px;
	margin: 0;
}

@media (min-width: 768px) {
	.paging {
		margin-left: 8px;
	}
}

.c-post {
	border-radius: 2px;
	margin-bottom: 24px;
	display: flex;
	flex-direction: column;
	box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
}

.c-post-header {
	border-bottom: 1px solid rgba(0, 0, 0, 0.125);
	flex: 1;
	align-self: auto;
}

.c-post-user {
	background-color: #f5f5f5;
	border-bottom: 1px solid rgba(0, 0, 0, 0.125);
	display: none;
	padding: 16px;
}

@media (min-width: 992px) {
	.c-post-user {
		display: block;
	}
}

.c-post-body {
	min-height: 192px;
	padding: 16px;
	display: flex;
	flex-direction: column;
}

@media (min-width: 992px) {
	.c-post-meta {
		display: flex;
		flex-direction: row;
	}
}

.c-post-title {
	font-size: 24px;
	letter-spacing: normal;
	font-weight: 400;
	line-height: 1.3333333333;
	margin-bottom: 0;
}

@media (min-width: 992px) {
	.c-post-title {
		padding-right: 8px;
		flex: 1;
		align-self: auto;
	}
}

.c-post-subtitle {
	font-size: 12px;
	letter-spacing: 0.08em;
	font-weight: 400;
	line-height: 1.6666666667;
	display: block;
}

.c-post-subtitle-item:not(:first-child) {
	padding-left: 8px;
}

.c-copy {
	font-size: 16px;
	letter-spacing: 0.04em;
	font-weight: 400;
	line-height: 1.75;
	font-weight: 300;
	margin: 16px 0 0;
	flex: 1;
	align-self: auto;
}

.c-copy > p {
	font-size: inherit;
	font-weight: inherit;
	line-height: inherit;
	margin-bottom: 16px;
}

.c-copy-bold {
	font-weight: bold;
}

.c-copy-italic {
	font-style: italic;
}

.c-copy-underline {
	text-decoration: underline;
}

.c-copy-list {
	font-size: inherit;
	font-weight: inherit;
	line-height: inherit;
	margin: 32px;
	margin-right: 0;
	padding-left: 16px;
}

.c-copy-list .c-copy-list {
	margin: 8px 24px;
	margin-right: 0;
}

.c-copy-smilies {
	vertical-align: text-bottom;
}

.c-copy-emoji {
	width: 1em;
	min-width: 18px;
	height: 1em;
	min-height: 18px;
}

.c-post-fluid .c-post-title {
	font-size: calc(24PX + (36 - 24) * ( (100vw - 400PX) / ( 1100 - 400) ));
	line-height: calc(36PX + (48 - 36) * ( (100vw - 400PX) / ( 1100 - 400) ));
}

.c-post-fluid .c-copy {
	font-size: calc(14PX + (21 - 14) * ( (100vw - 400PX) / ( 1100 - 400) ));
	line-height: calc(24PX + (36 - 24) * ( (100vw - 400PX) / ( 1100 - 400) ));
}

.c-menu-container {
	text-align: left;
	background-color: #ffffff;
	border-radius: 2px;
	position: absolute;
	z-index: 999;
	top: auto;
	right: 0;
	display: none;
	overflow: auto;
	box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 2px 1px -1px rgba(0, 0, 0, 0.2), 0 1px 3px 0 rgba(0, 0, 0, 0.12);
}

.c-menu {
	margin: 0;
	padding: 8px 0;
	list-style: none;
}

.c-menu-item {
	position: relative;
}

.c-menu-toggle {
	cursor: pointer;
}

.c-menu-toggle:hover > .c-menu-container,
.c-menu-toggle:hover a > .c-menu-container, .c-menu-toggle:focus > .c-menu-container,
.c-menu-toggle:focus a > .c-menu-container {
	display: block;
}

.c-menu-link {
	font-size: 14px;
	letter-spacing: normal;
	font-weight: 500;
	line-height: normal;
	white-space: nowrap;
	text-decoration: none;
	color: #212121;
	align-items: center;
	height: 48px;
	padding: 0 16px 0 16px;
	cursor: pointer;
	display: flex;
	flex-direction: row;
}

.c-menu-link-icon {
	color: #757575;
	width: 32px;
	height: 32px;
	margin-right: 24px;
}

.c-menu-link:hover {
	text-decoration: none;
	background-color: #eeeeee;
	color: #258cf4;
}

.c-menu-link:hover .c-menu-link-icon {
	color: #258cf4;
}

.c-menu-divider {
	background-color: rgba(0, 0, 0, 0.125);
	height: 1px;
	margin-top: 8px;
	margin-bottom: 8px;
}

.c-paging {
	margin: 0;
	padding: 0;
	list-style: none;
	align-content: center;
	display: flex;
	flex-direction: row;
}

.c-paging-item {
	margin: 0;
}

.c-paging-item:first-of-type .c-paging-link {
	border-top-left-radius: 2px;
	border-bottom-left-radius: 2px;
}

.c-paging-item:last-of-type .c-paging-link {
	border-top-right-radius: 2px;
	border-bottom-right-radius: 2px;
}

.c-paging-link {
	font-size: 18px;
	line-height: 1.6666666667;
	background: #f5f5f5;
	margin: 0;
	padding: 4px 8px;
}

.c-paging-link:hover {
	text-decoration: none;
	background: #eeeeee;
	border-radius: 2px;
}

.c-paging-link-icon {
	width: 24px;
	height: 24px;
}

.c-paging-item:first-of-type .c-paging-link,
.c-paging-item:last-of-type .c-paging-link {
	padding: 4px;
}

.c-toolbar {
	background-color: #f5f5f5;
	border-radius: 2px;
	position: relative;
	display: inline-block;
	margin: 0;
	margin-bottom: 16px;
	padding: 0;
	list-style: none;
	box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
}

.c-toolbar-item {
	position: relative;
	display: inline-block;
}

.c-toolbar-action {
	font-size: 14px;
	letter-spacing: normal;
	text-transform: uppercase;
	font-weight: 500;
	line-height: 2.7142857143;
	text-align: center;
	vertical-align: middle;
	white-space: nowrap;
	text-decoration: none;
	outline: none;
	color: #757575;
	position: relative;
	display: inline-block;
	overflow: hidden;
	margin: 0;
	padding: 0 16px;
	cursor: pointer;
	user-select: none;
	transition: box-shadow 0.2s cubic-bezier(0.4, 0, 1, 1), background-color 0.2s cubic-bezier(0.4, 0, 0.2, 1), color 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}

.c-toolbar-action:hover, .c-toolbar-action:focus {
	background-color: #eeeeee;
	color: #258cf4;
}

.c-toolbar-icon:hover {
	text-decoration: none;
}
